html, body, #app {
  height: 100% !important;
  width: 100% !important;
  padding: 0px !important;
  margin: 0px !important;
  line-height: normal !important;
  overflow-y: auto !important;
}
.h-100 {
  height: 100% !important;
}
.w-100 {
  width: 100% !important;
}
.d-block {
  display: block !important;
}
.d-none {
  display: none !important;
}
.tikets-main-content {
  display: grid;
  height: 100%;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: "filter" "table";
}
.el-table__row:hover {
  cursor: pointer;
}
.el-table__body-wrapper{
  overflow-y: auto !important;
}
/*text styles*/
.sentence-capitalized::first-letter{
  text-transform: uppercase;
}


/*standart tabs*/
.standart-tab{
  display: grid;
  height: 100%;
  overflow:hidden;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "standart-tab__toolbar"
    "standart-tab__card";
}
.standart-tab__toolbar{
  grid-area: standart-tab__toolbar;
}
.standart-tab__toolbar .btn{
  margin:2px;
}
.standart-tab__card{
  grid-area: standart-tab__card;
  overflow: hidden;
}
.standart-tab__card .el-table {
  height:100%;
  display: grid;
  grid-template-rows: auto auto 1fr;
  overflow: hidden;
}
.standart-tab__card .el-table .el-table__header-wrapper, .standart-tab__card .el-table .el-table__body-wrapper{
  width:auto !important;
}


::-webkit-scrollbar-button, ::-moz-scrollbar-button {
  background-image:url('');
  background-repeat:no-repeat;
  width:5px;
  height:0px
  }
  
  ::-webkit-scrollbar-track, ::-moz-scrollbar-track {
  background-color:#ecedee
  }
  
  ::-webkit-scrollbar-thumb, ::-moz-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background-color:#6dc0c8;
  }
  
  ::-webkit-scrollbar-thumb:hover, ::-moz-scrollbar-thumb:hover{
  background-color:#56999f;
  }
  
  ::-webkit-resizer, ::-moz-resizer{
  background-image:url('');
  background-repeat:no-repeat;
  width:4px;
  height:0px
  }
  
  ::-webkit-scrollbar, ::-moz-scrollbar{
  width: 4px;
  }


