
section#main-layout[data-v-07b52b6d] {
  height: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: -webkit-min-content 1fr;
  grid-template-columns: min-content 1fr;
  grid-template-rows: -webkit-min-content min-content auto -webkit-min-content;
  grid-template-rows: min-content min-content auto min-content;
  grid-template-areas: "header header" "toolbar toolbar" "aside main" "footer footer";
}
section#main-layout>header[data-v-07b52b6d] {
  grid-area: header;
}
section#main-layout>nav[data-v-07b52b6d] {
  grid-area: toolbar;
}
section#main-layout>aside[data-v-07b52b6d] {
  grid-area: aside;
}
section#main-layout>main[data-v-07b52b6d] {
  grid-area: main;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
}
section#main-layout>footer[data-v-07b52b6d] {
  grid-area: footer;
}



