
.dashboard-main{
  overflow: auto;
}
.dashboard-component .system-bar{
  cursor:pointer;
}
.dashboard-component{
  width:350px;
}
.dashboard-grid{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  list-style-type: none;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap
}
.dashboard-chart{
  margin: auto;
}
.highcharts-credits{
  display:none
}
.dashboard__dialog{
  display: grid;
  grid-template-rows: auto 1fr;
}




