
.vd-card{
  display: grid;
  grid-template-rows: auto 1fr;
}
.vd-card.maximized{
  position:fixed !important;
  top:0px !important;
  left:0px !important;
  bottom:0px !important;
  right:0px !important;
  margin:0px !important;
  z-index:1000 !important;
  -webkit-transition: 1s;
  transition: 1s;
}



