
#main-layout > aside > .ant-layout-sider{
  height:100vh;
}
#main-layout > aside > .ant-layout-sider > .ant-layout-sider-children > .ant-menu{
  /*height:100vh;*/
  overflow: auto;
}
ul.main-menu {
  height: 100%;
}
ul.main-menu:not(.el-menu--collapse) {
  width: 250px;
}
ul.main-menu .toolbar__content>:not(.btn):not(.menu):first-child:not(:only-child), ul.main-menu .toolbar__extension>:not(.btn):not(.menu):first-child:not(:only-child){
  margin-left: 1px;
}
ul.main-menu .toolbar__content>:not(.btn):not(.menu):last-child:not(:only-child), ul.main-menu .toolbar__extension>:not(.btn):not(.menu):last-child:not(:only-child){
  margin-right: 1px;
}
